.about {
    text-align: center;
    margin: 0px;
    padding: 0px;
    background-color: #f9f9f9; /* Light background for contrast */
    border-radius: none;
    box-shadow: 0px 5px rgba(0, 0, 0, 0.1);
    background-image: url("./doodle_aboutpage_bg.jpg") ;
    min-height: 75vh;
}

.about h1 {
    font-size: 2.5em;
    text-align: center;
    margin-bottom: 5px;
    color: #333;
    padding: 50px 3px;
}

/* .about h2 {
    margin-top: 20px;
