.gallery {
    padding: 20px;
    text-align: center;
}

.gallery h1 {
    margin-bottom: 20px;
    color: #333;
}

.gallery-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 10px; /* Space between items */
    overflow-y: auto; /* Enable vertical scrolling */
    max-height: 80vh; /* Limit height of the gallery */
}

.gallery-item {
    overflow: hidden;
    border-radius: 8px; /* Rounded corners */
}

.gallery-item img {
    width: 100%;
    height: auto;
    display: block; /* Remove bottom space */
    transition: transform 0.3s; /* Animation for zoom effect */
}

.gallery-item img:hover {
    transform: scale(1.05); /* Zoom effect on hover */
}
