body {
    margin: 0;
    font-family: 'Poppins', sans-serif;
}

.contact-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: linear-gradient(135deg, #caacb1, #fef4f5); /* Soft gradient with lighter tones */
    padding: 2rem;
}

h1 {
    color: #4a2c30; /* Darker accent color for contrast */
    margin-bottom: 2rem;
}

.contact-form {
    background: rgba(255, 255, 255, 0.9);
    padding: 2rem;
    border-radius: 8px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 400px;
}

input,
textarea {
    width: 100%;
    padding: 1rem;
    margin-bottom: 1rem;
    border: 1px solid #e4aab2; /* Matching the specified pink */
    border-radius: 4px;
    outline: none;
}

input::placeholder,
textarea::placeholder {
    color: #d1a6ae; /* Slightly darker placeholder color */
}

button {
    padding: 1rem;
    background: #e4aab2; /* Main button color */
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background 0.3s;
}

button:hover {
    background: #d58a96; /* Darker shade on hover */
}
