.home {
    text-align: center;
    margin: 0px;
    background-image: url('./cat_bg_homepage.jpg'); /* Path relative to the CSS file */
    background-size:cover; /* Ensures the image covers the entire area */
    background-position: center; /* Centers the image */
    padding: 0px; /* Optional padding */
    color: black; /* Change text color if needed */
    min-height: 80vh; /* Makes sure the background covers the entire viewport height */
    
}


.hero {
    color: rgb(46, 46, 46);
    align-items: center;
    padding: 100px 10px;
    border-radius: none;
    
}

.hero h1 {
    font-size: 2.5em;
    margin-bottom: 10px;
}

.hero p {
    font-size: 1.2em;
    margin-bottom: 20px;
}

.cta-button {
    background-color: rgb(46, 46, 46);
    color: white;
    border: none;
    padding: 10px 10px;
    font-size: 1em;
    border-radius: none;
    cursor: pointer;
    transition: background-color 0.3s;
    
}

.cta-button:hover {
    background-color: #535353;
}

.content-blocks {
    display: flex;
    justify-content: space-around;
    margin-top: 20px;
}

.block {
    background-color: #f4f4f4;
    border-radius: none;
    padding: 20px;
    width: 30%;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.block h2 {
    color: #333;
}

.block p {
    color: #666;
}
